import React, {FormEvent} from 'react';
import {Typography, Button, Divider} from "@material-ui/core";
import {Elements, CardElement, ElementsConsumer} from "@stripe/react-stripe-js";
import {loadStripe} from "@stripe/stripe-js";

import {CheckoutToken} from "./Checkout/Checkout";
import {Review} from './Review';
import {CheckoutCapture} from "@chec/commerce.js/types/checkout-capture";

type PaymentFormPropsType = {
    shippingData: any
    checkoutToken: CheckoutToken
    backStep: () => void
    nextStep: () => void
    onCaptureCheckout: (checkoutTokenId: string, newOrder: CheckoutCapture) => void
    timeout: () => void
}

const stripePromise = loadStripe(process.env.REACT_APP_STRIPE_PUBLIC_KEY as string);

export const PaymentForm = ({shippingData, checkoutToken, backStep, onCaptureCheckout, nextStep, timeout}: PaymentFormPropsType) => {

    const handleSubmit = async (e: FormEvent, elements: any, stripe: any) => {
        e.preventDefault()

        if (!stripe || elements) return
        const cardElement = elements.getElement(CardElement)
        const {error, paymentMethod} = await stripe.paymentMethod({type: 'card', card: cardElement})

        if (error) {
            console.log(error)
        } else {
            const orderData = {
                line_items: checkoutToken.live.line_items,
                customer: {firstname: shippingData.firstName, lastname: shippingData.lastName, email: shippingData.email},
                shipping: {
                    name: 'Primary',
                    street: shippingData.address1,
                    town_city: shippingData.town_city,
                    county_state: shippingData.shippingSubdivision,
                    postal_zip_code: shippingData.zip,
                    country: shippingData.shippingCountry,
                },
                fulfilment: {shipping_method: shippingData.shippingOption},
                payment: {
                    gateway: 'stripe',
                    stripe: {
                        payment_method_id: paymentMethod.id
                    }
                }
            }
            onCaptureCheckout(checkoutToken.id, orderData)
            timeout()
            nextStep()
        }
    }

    return (
        <>
            <Review checkoutToken={checkoutToken}/>
            <Divider/>
            <Typography variant="h6" gutterBottom style={{margin: '20px 0'}}>Payment method</Typography>
            <Elements stripe={stripePromise}>
                <ElementsConsumer>{({elements, stripe}) => (
                    <form onSubmit={(e) => handleSubmit(e, elements, stripe)}>
                        <CardElement/>
                        <br/> <br/>
                        <div style={{display: 'flex', justifyContent: 'space-between'}}>
                            <Button variant="outlined" onClick={backStep}>Back</Button>
                            <Button type="submit" variant="contained" disabled={!stripe} color="primary">
                                Pay {checkoutToken?.live.subtotal.formatted_with_symbol}
                            </Button>
                        </div>
                    </form>
                )}
                </ElementsConsumer>
            </Elements>
        </>
    );
};

